'use client';

import React, { useState, useEffect } from 'react';
import Board from '@/components/Board';
import { getInitialState, makeMove as engineMakeMove } from '@/lib/chess-engine';
import { Move, GameState } from '@/types/chess';
import { getPusherClient } from '@/lib/pusher';
import { useStats } from '@/hooks/useStats';

export default function GamePage() {
  const [gameState, setGameState] = useState<GameState>(getInitialState());
  const [playerSide, setPlayerSide] = useState<'red' | 'black' | null>(null);
  const [gameId, setGameId] = useState<string | null>(null);
  const [isCopied, setIsCopied] = useState(false);
  const { stats, addWin, addLoss } = useStats(playerSide);

  // Initialize Game ID from URL hash or generate new one
  useEffect(() => {
    const hash = window.location.hash.replace('#', '');
    if (hash) {
      setGameId(hash);
    } else {
      const newId = Math.random().toString(36).substring(2, 9);
      setGameId(newId);
      window.location.hash = newId;
    }
  }, []);

  // Subscribe to Pusher channel
  useEffect(() => {
    if (!gameId) return;

    const pusher = getPusherClient();
    if (!pusher) return;

    const channel = pusher.subscribe(`game-${gameId}`);

    channel.bind('move-made', (data: { move: Move; state: GameState }) => {
      setGameState(data.state);

      // Update stats if game ended
      if (data.state.winner) {
        if (playerSide === data.state.winner) {
          addWin();
        } else if (playerSide && playerSide !== data.state.winner) {
          addLoss();
        }
      }
    });

    return () => {
      pusher.unsubscribe(`game-${gameId}`);
    };
  }, [gameId, playerSide, addWin, addLoss]);

  const handleMove = async (move: Move) => {
    // Prevent moving if it's not our turn (in multiplayer mode)
    if (playerSide && gameState.turn !== playerSide) return;

    const nextState = engineMakeMove(gameState, move);
    if (nextState) {
      setGameState(nextState);

      // Broadcast move via API
      if (gameId) {
        try {
          await fetch('/api/move', {
            method: 'POST',
            body: JSON.stringify({ move, state: nextState, gameId }),
            headers: { 'Content-Type': 'application/json' },
          });
        } catch (error) {
          console.error('Failed to broadcast move:', error);
        }
      }
    }
  };

  const copyLink = () => {
    navigator.clipboard.writeText(window.location.href);
    setIsCopied(true);
    setTimeout(() => setIsCopied(false), 2000);
  };

  if (!gameId) return <div className="min-h-screen bg-neutral-900 flex items-center justify-center text-white font-serif">Ê≠£Âú®ÂàùÂßãÂåñ...</div>;

  return (
    <main className="min-h-screen bg-neutral-900 text-white flex flex-col items-center justify-center p-4">
      <div className="max-w-4xl w-full flex flex-col gap-8">
        <header className="flex flex-col md:flex-row gap-4 justify-between items-center bg-neutral-800 p-6 rounded-xl shadow-lg border border-neutral-700">
          <div>
            <h1 className="text-3xl font-extrabold tracking-tight bg-gradient-to-r from-red-500 to-amber-500 bg-clip-text text-transparent">
              ‰∏≠ÂõΩË±°Ê£ã
            </h1>
            <p className="text-neutral-400">Ê∏∏Êàè ID: <span className="text-amber-500 font-mono">{gameId}</span></p>
          </div>

          <div className="flex flex-wrap gap-4 items-center justify-center">
            {!playerSide ? (
              <div className="flex gap-2 bg-neutral-700 p-1 rounded-lg">
                <button onClick={() => setPlayerSide('red')} className="px-4 py-2 bg-red-600 hover:bg-red-500 rounded-md transition-colors">Âü∑Á¥Ö</button>
                <button onClick={() => setPlayerSide('black')} className="px-4 py-2 bg-neutral-900 hover:bg-neutral-800 rounded-md transition-colors">Âü∑Èªë</button>
              </div>
            ) : (
              <div className="bg-neutral-700 px-4 py-2 rounded-lg border border-neutral-600">
                <span className="text-neutral-400 text-sm block">ÊàëÁöÑË∫´‰ªΩ</span>
                <span className={playerSide === 'red' ? 'text-red-500 font-bold' : 'text-white font-bold'}>
                  {playerSide === 'red' ? 'üî¥ Á¥ÖÊñπ' : '‚ö´ ÈªëÊñπ'}
                </span>
              </div>
            )}

            <button
              onClick={copyLink}
              className="bg-amber-600 hover:bg-amber-500 text-white px-4 py-2 rounded-lg transition-colors flex items-center gap-2"
            >
              {isCopied ? 'Â∑≤Â§çÂà∂ÔºÅ' : 'ÂàÜ‰∫´ÈìæÊé•ÁªôÂÆ∂‰∫∫'}
            </button>
          </div>
        </header>

        <section className="flex justify-center p-4 md:p-8 bg-neutral-800 rounded-2xl shadow-2xl border border-neutral-700 relative overflow-hidden">
          <div className="absolute top-0 left-0 w-full h-full opacity-5 pointer-events-none">
            <div className="absolute top-10 left-10 w-64 h-64 rounded-full bg-red-500 blur-3xl"></div>
            <div className="absolute bottom-10 right-10 w-64 h-64 rounded-full bg-amber-500 blur-3xl"></div>
          </div>

          <Board
            gameState={gameState}
            onMove={handleMove}
            playerSide={playerSide || 'red'}
          />
        </section>

        <footer className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="bg-neutral-800 p-4 rounded-xl border border-neutral-700">
            <h3 className="text-neutral-400 text-sm mb-2 uppercase tracking-wider font-bold">ÊàëÁöÑÊàòÁª©</h3>
            <div className="flex justify-between items-end">
              <span className="text-2xl font-bold">{stats.wins} ËÉú / {stats.losses} Ë¥ü</span>
              <span className="text-neutral-500">
                ËÉúÁéá {stats.wins + stats.losses > 0 ? Math.round((stats.wins / (stats.wins + stats.losses)) * 100) : 0}%
              </span>
            </div>
          </div>
          <div className="bg-neutral-800 p-4 rounded-xl border border-neutral-700 flex justify-between items-center">
            <div>
              <h3 className="text-neutral-400 text-sm mb-2 uppercase tracking-wider font-bold">ÂÆûÊó∂ÂêåÊ≠•</h3>
              <div className="flex items-center gap-2">
                {getPusherClient() ? (
                  <>
                    <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
                    <span className="text-green-500">Pusher Â∑≤Â∞±Áª™</span>
                  </>
                ) : (
                  <>
                    <div className="w-3 h-3 bg-red-500 rounded-full"></div>
                    <span className="text-red-500">ÂæÖÈÖçÁΩÆ Pusher ÂØÜÈí•</span>
                  </>
                )}
              </div>
            </div>
            <button
              onClick={() => setGameState(getInitialState())}
              className="text-neutral-500 hover:text-white text-sm underline"
            >
              ÈáçÁΩÆÊ£ãÂ±Ä
            </button>
          </div>
        </footer>
      </div>
    </main>
  );
}
