'use client';

import React, { useState } from 'react';
import { GameState, Move, Position } from '@/types/chess';
import { isValidMove } from '@/lib/chess-engine';
import { clsx, type ClassValue } from 'clsx';
import { twMerge } from 'tailwind-merge';
import '../app/board.css';

function cn(...inputs: ClassValue[]) {
    return twMerge(clsx(inputs));
}

interface BoardProps {
    gameState: GameState;
    onMove: (move: Move) => void;
    playerSide: 'red' | 'black';
}

const PIECE_NAMES: Record<string, string> = {
    'red-K': 'Â∏•', 'black-K': 'Â∞á',
    'red-A': '‰ªï', 'black-A': 'Â£´',
    'red-E': 'Áõ∏', 'black-E': 'Ë±°',
    'red-H': 'ÂÇå', 'black-H': 'È¶¨',
    'red-R': '‰ø•', 'black-R': 'Ëªä',
    'red-C': 'Á†≤', 'black-C': 'ÁÇÆ',
    'red-P': 'ÂÖµ', 'black-P': 'Âçí',
};

export default function Board({ gameState, onMove, playerSide }: BoardProps) {
    const [selected, setSelected] = useState<Position | null>(null);

    const handleClick = (r: number, c: number) => {
        if (gameState.winner) return;

        if (selected) {
            if (selected.r === r && selected.c === c) {
                setSelected(null);
                return;
            }
            const move: Move = { from: selected, to: { r, c } };
            if (isValidMove(gameState, move)) {
                onMove(move);
                setSelected(null);
            } else {
                const piece = gameState.board[r][c];
                if (piece && piece.player === gameState.turn) {
                    setSelected({ r, c });
                } else {
                    setSelected(null);
                }
            }
        } else {
            const piece = gameState.board[r][c];
            if (piece && piece.player === gameState.turn) {
                setSelected({ r, c });
            }
        }
    };

    const renderGrid = () => {
        const lines = [];
        // Horizontal lines
        for (let i = 0; i < 10; i++) {
            lines.push(
                <div key={`h-${i}`} className="line-h" style={{ top: `${i * 11.11}%` }} />
            );
        }
        // Vertical lines (split by river)
        for (let i = 0; i < 9; i++) {
            // Full vertical lines for outer edges
            if (i === 0 || i === 8) {
                lines.push(
                    <div key={`v-${i}`} className="line-v" style={{ left: `${i * 12.5}%` }} />
                );
            } else {
                // Upper part
                lines.push(
                    <div key={`v-u-${i}`} className="line-v" style={{
                        left: `${i * 12.5}%`,
                        bottom: '55.55%',
                        height: '44.45%'
                    }} />
                );
                // Lower part
                lines.push(
                    <div key={`v-l-${i}`} className="line-v" style={{
                        left: `${i * 12.5}%`,
                        top: '55.55%',
                        height: '44.45%'
                    }} />
                );
            }
        }

        // Palaces
        // Black Palace (top)
        lines.push(
            <div key="palace-black" className="palace-x" style={{ left: '37.5%', top: '0', width: '25%', height: '22.22%' }} />
        );
        // Red Palace (bottom)
        lines.push(
            <div key="palace-red" className="palace-x" style={{ left: '37.5%', bottom: '0', width: '25%', height: '22.22%' }} />
        );

        return lines;
    };

    const renderPieces = () => {
        const elements = [];
        for (let r = 0; r < 10; r++) {
            for (let c = 0; c < 9; c++) {
                const piece = gameState.board[r][c];
                const isSelected = selected?.r === r && selected?.c === c;

                elements.push(
                    <div
                        key={`point-${r}-${c}`}
                        className={cn(
                            "piece-slot",
                            isSelected && "selected"
                        )}
                        style={{ top: `${r * 11.11}%`, left: `${c * 12.5}%` }}
                        onClick={() => handleClick(r, c)}
                    >
                        {piece && (
                            <div className={cn("piece-inner", piece.player)}>
                                {PIECE_NAMES[`${piece.player}-${piece.type}`]}
                            </div>
                        )}

                        {/* Move Hints */}
                        {selected && isValidMove(gameState, { from: selected, to: { r, c } }) && (
                            <div className={piece ? "capture-hint" : "move-hint"} />
                        )}
                    </div>
                );
            }
        }
        return elements;
    };

    return (
        <div className="flex flex-col items-center gap-6">
            <div className="board-container">
                {/* Board Lines */}
                <div className="board-lines">
                    {renderGrid()}
                    <div className="river-bank">
                        <div className="flex justify-between w-full px-12 opacity-60">
                            <span>Ê•ö Ê≤≥</span>
                            <span>Êº¢ Áïå</span>
                        </div>
                    </div>
                </div>

                {/* Pieces Layer */}
                <div className="pieces-layer">
                    {renderPieces()}
                </div>
            </div>

            <div className="flex gap-10 items-center">
                <div className={cn(
                    "flex items-center gap-3 px-6 py-3 rounded-full border-2 transition-all",
                    gameState.turn === 'red' ? "bg-red-600/20 border-red-600 text-red-500 shadow-lg shadow-red-900/20" : "bg-neutral-800 border-neutral-700 text-neutral-500"
                )}>
                    <span className="w-3 h-3 rounded-full bg-red-600"></span>
                    <span className="font-bold">Á∫¢Êñπ {gameState.turn === 'red' && "ËêΩÂ≠ê‰∏≠"}</span>
                </div>

                <div className={cn(
                    "flex items-center gap-3 px-6 py-3 rounded-full border-2 transition-all",
                    gameState.turn === 'black' ? "bg-white/10 border-white text-white shadow-lg shadow-white/10" : "bg-neutral-800 border-neutral-700 text-neutral-500"
                )}>
                    <span className="w-3 h-3 rounded-full bg-white"></span>
                    <span className="font-bold">ÈªëÊñπ {gameState.turn === 'black' && "ËêΩÂ≠ê‰∏≠"}</span>
                </div>
            </div>

            {gameState.winner && (
                <div className="fixed inset-0 bg-black/80 flex items-center justify-center z-[100] backdrop-blur-sm animate-in fade-in duration-300">
                    <div className="bg-neutral-800 border border-neutral-700 p-10 rounded-3xl shadow-2xl text-center max-w-sm w-full mx-4">
                        <div className={cn(
                            "w-20 h-20 rounded-full mx-auto mb-6 flex items-center justify-center text-4xl shadow-inner",
                            gameState.winner === 'red' ? "bg-red-600 text-white" : "bg-white text-black"
                        )}>
                            {gameState.winner === 'red' ? 'Á¥Ö' : 'Èªë'}
                        </div>
                        <h2 className="text-3xl font-black mb-2 tracking-tight">
                            {gameState.winner === 'red' ? 'Á∫¢Êñπ' : 'ÈªëÊñπ'} ÂÆåËÉúÔºÅ
                        </h2>
                        <p className="text-neutral-400 mb-8 font-medium">Ê£ãÂ±ÄÁªà‰∫ÜÔºåÁ≤æÂΩ©Áªù‰º¶„ÄÇ</p>
                        <button
                            className="w-full bg-gradient-to-r from-amber-500 to-orange-600 text-white font-bold px-8 py-4 rounded-2xl hover:scale-105 transition-transform shadow-xl shadow-orange-900/20"
                            onClick={() => window.location.reload()}
                        >
                            ÈáçÂºÄ‰∏ÄÂ±Ä
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
}
