import { useState, useEffect } from 'react';

interface Stats {
    wins: number;
    losses: number;
    draws: number;
}

export function useStats(side: 'red' | 'black' | null) {
    const [stats, setStats] = useState<Stats>({ wins: 0, losses: 0, draws: 0 });

    useEffect(() => {
        const saved = localStorage.getItem('chess_stats');
        if (saved) {
            setStats(JSON.parse(saved));
        }
    }, []);

    const addWin = () => {
        const newStats = { ...stats, wins: stats.wins + 1 };
        setStats(newStats);
        localStorage.setItem('chess_stats', JSON.stringify(newStats));
    };

    const addLoss = () => {
        const newStats = { ...stats, losses: stats.losses + 1 };
        setStats(newStats);
        localStorage.setItem('chess_stats', JSON.stringify(newStats));
    };

    const addDraw = () => {
        const newStats = { ...stats, draws: stats.draws + 1 };
        setStats(newStats);
        localStorage.setItem('chess_stats', JSON.stringify(newStats));
    };

    return { stats, addWin, addLoss, addDraw };
}
